import React, { useState } from 'react';
import { Button, Modal, ModalHeader, ModalBody, ModalFooter } from 'reactstrap';

const ModalExample = ({ name }) => {
  const [modal, setModal] = useState(true);

  const toggle = () => setModal(!modal);

  return (
    <div>
      <Modal isOpen={modal} toggle={toggle}>
        <ModalHeader toggle={toggle}>Ateno</ModalHeader>
        <ModalBody>
          <p>Ol {name}.</p>
          <p>
            A atual verso do sistema est aberta para os(as) candidatos(as) a
            vereador(a). Se voc  eleitor(a), pode responder agora e suas
            respostas ficaro salvas mas o match ainda no estar disponvel.
          </p>
          <p>
            <strong>Candidatos(as): </strong> lembrem-se que, para efetivar a
            sua participao no Vota Cidade, voc precisa responder a todas as
            questes. As justificativas so opcionais.
          </p>
          <p>
            Seja bem-vindo(a) e aproveite o Vota Cidade para se aproximar dos
            seus eleitores(as)!
          </p>
        </ModalBody>
        <ModalFooter>
          <Button color="primary" onClick={toggle}>
            Fechar
          </Button>
        </ModalFooter>
      </Modal>
    </div>
  );
};

export default ModalExample;
